}

.p-about {
  padding-top: 6.25rem;
  padding-bottom: 6.25rem;
  position: relative;
  overflow-x: clip;
}

.p-about::before {
  position: absolute;
  content: "";
  background: url(../images/common/about_bg_left.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 22.5rem;
  height: auto;
  aspect-ratio: 360/360;
  object-fit: cover;
  top: -3.5rem;
  left: -3.5rem;
}

.p-about__title {
  text-align: center;
}

.p-about__text-wrapper {
  margin-top: 3rem;
  text-align: center;
}

.p-about__text {
  font-size: 1rem;
  font-weight: 400;
  line-height: 2.25;
  color: #333;
}

.p-feature {
  padding-bottom: 7.5rem;
}

.p-feature__title {
  text-align: center;
}

.p-feature__grid {
  display: grid;
  margin-top: 5.25rem;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.75rem;
}

.p-feature__grid-list {
  height: 100%;
  position: relative;
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.1));
}

.p-feature__grid-list::before {
  position: absolute;
  content: attr(data-number) "";
  background: #990000;
  border-radius: 9999px;
  color: #fff;
  font-family: "source-han-serif-japanese", serif;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.375rem;
  text-align: center;
  width: 3.125rem;
  height: 3.125rem;
  top: -1.5rem;
  left: 50%;
  transform: translateX(-50%);
}

.p-feature__img img {
  aspect-ratio: 370/250;
  object-fit: cover;
  width: 100%;
  height: auto;
}

.p-feature__detail {
  background: #990000;
  padding: 1.5rem;
  max-height: 19.375rem;
  height: 100%;
}

.p-feature__text-wrapper {
  display: flex;
  gap: 0.5rem;
}

.p-feature__text-wrapper + .p-feature__text-wrapper {
  margin-top: 0.875rem;
}

.p-feature__text-wrapper svg {
  max-width: 1.5rem;
  min-width: 1.5rem;
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center center;
  margin-top: 0.25rem;
}

.p-feature__text-title {
  text-align: center;
  color: #fff;
  font-family: "source-han-serif-japanese", serif;
  font-size: 1.5625rem;
  font-weight: 600;
  line-height: 1.44;
  margin-bottom: 1.5rem;
}

.p-feature__text {
  font-size: 1rem;
  color: #fff;
  font-weight: 400;
}

.p-feature__flex {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.75rem;
  margin-top: 5.25rem;
  max-width: 49.125rem;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.p-feature__detail--white .p-feature__detail {
  background: #fff;
}

.p-feature__detail--white .p-feature__text-title {
  color: #333;
}

.p-feature__detail--white .p-feature__text {
  color: #333;
}

